html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  padding: 0;
  margin: 0;
  color: #1b1c21; }

body {
  margin: 0; }

.container {
  padding: 0px 20px;
  max-width: 1120px;
  margin: 0 auto; }

.brands__container {
  padding-top: 20px !important;
  background-color: #f8f8f8;
  padding-bottom: 20px !important;
  border-bottom: 1px solid #d9fff5; }

h2 {
  font-weight: 700;
  font-family: 'TT Lakes', sans-serif;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6000000238418579px; }

h3,
a {
  font-weight: 600;
  font-family: TT Lakes;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.20000000298023224px; }

p {
  font-weight: 500;
  font-family: TT Lakes;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.20000000298023224px; }

.main__repair {
  margin: 15px 0; }

.repair__h3 {
  margin-top: 20px;
  color: #7e7e82;
  font-weight: 500; }

.brands__swiper.swiper {
  height: 110px;
  width: 100%; }

.main__brands {
  padding-top: 20px; }

.brands__h3show {
  display: none; }

.repair__container.container {
  background-color: white; }

.swiper .swiper-slide {
  width: auto;
  min-width: 0;
  height: 76px; }

.swiper-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #eaeaea;
  min-width: 0;
  border-radius: 6px;
  padding: 16px;
  width: 220px;
  height: 30px; }

.swiper__btn {
  border: none;
  background-image: url(./static/go.svg);
  width: 28px;
  height: 38px;
  background-color: inherit; }

.swiper .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #aaaaaa; }

.swiper-pagination-bullet-active {
  background-color: #b5b6bc !important; }

.brands__swiper.-non-slider .swiper-wrapper {
  transform: inherit !important;
  display: flex; }

.brands__flex {
  display: none; }

.brand__more {
  display: none; }

@media screen and (min-width: 768px) {
  .brands__swipierContainer {
    display: none !important; }
  .brands__flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 24px; }
  .brand__item {
    padding: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 224px;
    height: 72px;
    background-color: white;
    border: #eaeaea 1px solid;
    border-radius: 6px; }
  .brand__button {
    border: none;
    background-image: url(./static/go.svg);
    width: 40px;
    height: 40px;
    background-color: inherit; }
  .brands__h3show {
    display: block; }
  .brands__h3show::before {
    content: url(./static/expand.svg);
    transition: 1s; }
  .brands__h3show::before {
    transform: rotate(180deg); }
  .hidden {
    display: none; }
  .brand__more {
    margin-top: 20px;
    display: flex;
    border: none;
    background-color: inherit;
    align-items: center; }
  .more__img {
    margin-right: 10px;
    transition: 0.5s; }
  .more__img.reverse {
    transform: rotate(180deg); } }
